@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


.container {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, #153677, #4e085f);
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  overflow-x: hidden;
}

.done {
  text-decoration: line-through;
}

.todo-app {
  width: 100%;
  max-width: 540px;
  background-color: #fff;
  padding: 40px 30px 70px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: left;
  gap: 2rem;
  margin-right: 20px;
}

.app-title {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.app-title h2,
.app-title i {
  color: #002765;
}

.app-title i {
  font-size: 26px;
}

.row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #edeef0;
  border-radius: 30px;
}

input {
  width: 100%;
  border: none;
  outline: none;
  background-color: transparent;
  padding: 12px;
  font-size: 17px;
  padding-left: 20px;
}

button {
  border: none;
  cursor: pointer;
  outline: none;
  padding: 15px 50px;
  border-radius: 30px;
  background-color: #ff5945;
  color: #fff;
  font-size: 18px;
  transition: all .25s ease;
}

button:hover {
  filter: brightness(.8);
}

@media screen and (max-width: 400px) {
  button {
    padding: 15px;
    border-radius: 0 30px 30px 0;
  }
}

ul {
  list-style: none;
}

#list-container div {
  position: relative;
}

#list-container li {
  font-size: 17px;
  padding: 12px 8px 12px 50px;
  margin-top: 5px;
  user-select: none;
  cursor: pointer;
  position: relative;
  word-wrap: break-word;

}

ul li::before {
  position: absolute;
  content: '';
  width: 24px;
  height: 24px;
  border-radius: 50%;
  left: 0;
  border: 2px solid #ff5945;
}


ul .checked {
  color: #555;
  text-decoration: line-through;
}

ul .checked::before {
  background-color: #ff5945;
}

#list-container div .X {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #d80000;
  font-size: 18px;
  transition: all .2s ease;
}

span:hover {
  background-color: #c9c9c9;
  color: #e80808;
}